/**
* BigInt factory.
*
* @module @stdlib/bigint-ctor
*
* @example
* var BigInt = require( '@stdlib/bigint-ctor' );
*
* var v = BigInt( '1' );
* // returns <bigint>
*/

// MODULES //

var BigInt = require( './main.js' );


// EXPORTS //

module.exports = BigInt;
